import React from 'react';
import Bill from '../../bill_new/Bill';

import Consistence from '../../tools/Consistence';
import CommonRegex from '../../tools/CommonRegex';
import CommonFun from '../../tools/CommonFun';

const PK = 'pk';
// :
const User = () => {
    // 
    const tableId = "new_user";
    
    // 
    const headMeta = {
        pk: {
            type: 'str', desc: '',
            __renderConfig: {
                isQuery: false,
                isList: false,
                isCard: false
            }
        },
        name: {
            type: 'str', desc: '',
        },
        phone: {
            type: 'str', desc: '',
            __validConfig: {
                matchReg: CommonRegex.phone,
            }
        },
        mail: {
            type: 'mail', desc: '',
            __validConfig: {
                matchReg: CommonRegex.mail,
                matchTips: '',
            },
        },
        age: {
            type: 'num', desc: '',
            __validConfig: {
                matchFun: CommonFun.isPositive,
                matchTips: '0'
            }
        },
        ageEditable: {
            type: 'checkbox', desc: '',
            __renderConfig: {
                isQuery: false
            }
        },
        // syncAge: {
        //     type: 'num', desc: 'sync',
        //     editable: false,
        //     __renderConfig: {
        //         isQuery: false
        //     }
        // },
    };
    
    // 
    const postMeta = {
        headPk: {
            type: 'str', desc: '',
            __renderConfig: {
                isQuery: false,
                isList: false,
                isCard: false
            }
        },
        pk: {
            type: 'str', desc: '',
            __renderConfig: {
                isQuery: false,
                isList: false,
                isCard: false,
                tableDesc: ''
            }
        },
        post: {
            type: 'str',
            desc: '',
        },
        level: {
            type: 'str',
            desc: '',
        }
    };
    
    const pointMeta = {
        headPk: {
            type: 'str', desc: '',
            __renderConfig: {
                isQuery: false,
                isList: false,
                isCard: false
            }
        },
        pk: {
            type: 'str', desc: '',
            __renderConfig: {
                isQuery: false,
                isList: false,
                isCard: false,
                tableDesc: ''
            }
        },
        point: {
            type: 'str',
            desc: ''
        },
    };

    
    const bodyMeta = {
        newPost: postMeta,
        newPoint: pointMeta,
    };
    const bodyTableAttr = {
        newPost: {
            title: ''
        },
        newPoint: {
            title: ''
        },
    };
    
    function onInit(queryTable, listTable, cardTable, cardBodyTableMap) {
    }

    function onQuery(queryCondition, table, refresh) {
        Consistence.query(tableId, queryCondition).then((res) => {
                Bill.handleRes(res, null, function (data) {
                    table.setSimpleData(data);
                    refresh();
                }, );
            });
    }

    function onBodyQuery(tableId, queryCondition, suchTable, refresh) {
        Consistence.query(tableId, queryCondition).then((res) => {
            suchTable.setSimpleData(res.data);
            refresh(res.data);
        })
    }

    /**
     * message, :
     *
     * 1. pk()
     * 2. (DB)
     *
     * ,
     *
     * :
     * {
     *  headData: { $tableId: headData},
     *  bodyData: [
     *              {$tableId: [data]}, 
     *              {$tableId: [data2]}
 *                  ]
     * }
     * 
     * tableId,
     * , onBillSave,tableId;
     *
     * :
     * {
     *  data: {
     *      $tableId: headData, 
     *      $tableId: bodyData1,
     *      $tableId: bodyData2
 *      },
     *  success: boolean,
     *  error: 'error msg such as error stack'
     *  }
     *
     * 
     *
     * data:,
     * success: ,
     * error: 
     **/
    function onSave(cardTable, cardBodyTableMap, refresh) {
        // 
        let headData = cardTable.getCurrentRow().getSimpleData();

        let bodyData = [];
        if(cardBodyTableMap) {
            for(let key of cardBodyTableMap.keys()) {
                let tempTable = cardBodyTableMap.get(key);
                let tempBodyData = tempTable.getSimpleData();
                let bodySet = {};
                bodySet[key] = tempBodyData; // keytableId
                bodyData.push(bodySet);
            }
        }
        
        let postData = {
            headData: { 'new_user': headData},
            bodyData: bodyData
        };

        Consistence.onBillSave(postData)
            .then((res) => {
                Bill.handleRes(res, '', function (headPk) {
                    let currRowData = cardTable.getCurrentRow().getSimpleData();
                    currRowData['pk'] = headPk;
                    refresh(currRowData);
                });
            });
    }

    function onDelete(data, callback) {
        var delPks = [];
        data.forEach((eachRow) => {
            delPks.push(eachRow[PK]);
        });
        Consistence.onDelete(tableId, delPks).then((res) => {
            callback(res);
        });
    }

    return (
        <Bill
            tableId={tableId}
            headMeta={headMeta}
            bodyMeta={bodyMeta}
            bodyTableAttr={bodyTableAttr}
            isQuery
            onQuery={onQuery}
            onBodyQuery={onBodyQuery}
            onDelete={onDelete}
            onSave={onSave}

            onInit={onInit}
        />
    )
};

export default User;


