import React, {Component} from 'react';
import Meta from '../../bill/Meta';
import {Row, Button} from 'antd';
import dm from '../../bill/tools/DefaultMeta';

// :
class MetaList extends Component {
    constructor() {
        super();
        this.state = {
            isEditable: false
        };
    }

    canEdit = () => {
        this.setState({
            isEditable: true
        })
    };

    canNotEdit = () => {
        this.setState({
            isEditable: false
        })
    };

    onChange = (key, value) => {
        console.log(': ' + key + ', : ' + value);
    };

    render() {
        /**
         * 
         */
        const metaAttrList = dm.fillDefaultMeta({
            data: {type: 'date', desc: ''},
            time: { type: 'time', desc: ''},
            num: { type: 'num', desc: ''},
            str: { type: 'str', desc: ''},
            mail: { type: 'mail', desc: ''},
            refer: { type: 'refer', desc: '', refer: {
                tableId: 'user',
                field: 'pk',
                renderField: 'name'
            }}
        });
        const keys = Object.keys(metaAttrList);

        const isEditable = this.state.isEditable;
        return (
            <div>
                <Row type="flex" justify="end" style={{ marginBottom: 16 }}>
                    {
                        isEditable
                            ?
                            <Button.Group>
                                <Button onClick={this.canNotEdit}></Button>
                            </Button.Group>
                            :
                            <Button.Group>
                                <Button onClick={this.canEdit}></Button>
                            </Button.Group>
                    }
                </Row>
                <Row gutter={16}>
                    {
                        keys.map((key)=> {
                            let metaAttr = metaAttrList[key];
                            return <Meta
                                key={metaAttr.type}
                                type={metaAttr.type}
                                field={ key }
                                meta={metaAttr}
                                editable={isEditable}
                                onChange={this.onChange}
                            />
                        })
                    }
                </Row>
            </div>
        )
    }
}

export default MetaList;


